<template>
    <div class="container">
      <h3 class="tx-c fz500">请登录</h3>
      <div class="content">
       <mt-field  placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
       <mt-field  placeholder="请输入密码" type="password" v-model="password"></mt-field>
       <mt-button class="login" size="large" type="primary" @click="doLogin">登录</mt-button>
      </div>
      <a class="login-phone fr" @click="$router.push('/phoneLogin')">手机号登录</a>
       <div class="fg tx-r">
         <span @click="$router.push('/findpwd')">忘记密码？</span>
      </div>
      <div class="foot fx fx-ac fx-jc">
          <span>还没有账号？</span>
          <mt-button style="font-size:14px;" @click="$router.push('/register')" type="default">立即注册</mt-button>
      </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui'
import { checkMobile } from '@/assets/js/util'
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      phone: '',
      password: ''
    }
  },
  methods: {
    ...mapActions(['setUserInfo']),
    doLogin () {
      if (!checkMobile(this.phone)) {
        Toast('手机号错误')
        return
      }
      if (this.phone === '' || this.password === '') {
        Toast('手机号和密码不能为空')
        return
      }
      let userInfo = {
        name: this.phone,
        pwd: this.password,
        tk: ''
      }
      this.$api.requestLogin(userInfo).then(res => {
        if (res.success) {
          this.setUserInfo(res)
          this.$router.push('/home')
        } else {
          Toast(res.msg)
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
@import '@/assets/css/config.scss';
.container{
    z-index: 100;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom:0;
    background: #fff;
    .content{
     padding: 0 20px;
     font-size: $font-size-medium;
     .login{
         margin-top: 20px;
     }
    }
    input{
        font-size: $font-size-small;
        border: 0;
        border-bottom: 1px solid #ccc;
    }
    .foot{
        position: absolute;
        width: 100%;
        bottom: 50px;
        font-size: 12px;
        color: #9c9c9c;
    }
}
.fg{
  margin-top: 10px;
  padding-right: 10px;
  color: #9c9c9c;
  span:active{
    color: #ccc;
  }
}
.login-phone{
  margin: 10px 20px 0 0;
  color: #9c9c9c;
  &:active{
    color: #ccc;
  }
}
</style>



